.author {
  display: flex;
  align-items: center;
}
.author__image-wrapper {
  width: 53px;
  height: 53px;
  overflow: hidden;
  border-radius: 50%;
  margin-right: 10px;
}
.author__name {
  font-size: 17px;
  line-height: 23px;
}
.author__name b {
  color: #FE4349;
  font-weight: normal;
}

@media (max-width: 1024px) {
  .author__name {
    font-size: 15px;
    line-height: 20px;
  }
}
@media (max-width: 767px) {
  .author__name {
    font-size: 14px;
    line-height: 22px;
  }
}